	module.exports = {"data":{"markdownRemark":{"html":"<p>政府のイメージに関する記事を書く：</p>\n<p>価値に焦点を当て、定義と5つの要素が提案されています。定義は：政府のイメージであり、これは特定の政府システム機能の全体的なパフォーマンスを意味する。政府イメージの評価は、自国の運営における政府システム全体の質、政府規制の品質指数、公共価値の評価における政府システムの運用プロセスと成果の認識の2つの側面から評価されます。インデックス。</p>\n<p>要素は、価値、信頼性、効率性、公平性、完全性を表します。評判は非常に重要です。政府は、完全な企業が自らの信頼性に注意を払うべきであるように、自らの信頼性を重視しなければならない。政府の信頼性は、同社の信頼性に似ています。企業の信頼性は、製品と企業の市場との関係によって決定されますが、信頼性がなければ当然市場は存在せず、企業自体には自然に存在と発展の余地がありません。政府の信用度は同じだが、信頼性がなければ人々の心理的アイデンティティと支援を失い、政府のエネルギーは限られる。政府の信頼性とは何か？政府の信頼性は、一般に、政府が支配する人々に対する政府の全体的な約束の練習である。中国は社会主義国家であり、全民族の利益のために政府の最大の目標であり、与党と政府が民衆を奉仕することを約束した。これは中国共産党が長年にわたって戦ってきた目標でもある。この全体的な約束を守る義務があります。政府の日常的な管理と政府の活動の間に政府の約束が実現できるかどうかは、政府が信頼を得るための最も基本的な要素です。高等教育機関であろうと草の根的組織であろうと、すべての行政職員は、高等教育機関であろうと中等教育機関であろうと、彼ら自身の実践において全体的な状況を達成しなければならない。約束する。全体的な約束はすべてのレベルの政府に分解され、すべてのレベルの政府も自らの活動において約束を守っているため、すべてのレベルの政府関係者が練習し、約束する必要があります。これは政府の完璧なイメージを形作るでしょう。政府関係者は、国民の利益を最上位に置かず、自分のプライベートな利益を第一に置いたり、任務を怠ったりしても、政府のイメージは当然のことながら高くはないだろう。そうです。</p>\n<p>政治的コミットメントは開発にふさわしい概念であり、中国の政治哲学を確立するための論理的出発点であるかもしれない。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0059.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0059.md","frontmatter":{"title":"3月15日 火曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0059.md absPath of file >>> MarkdownRemark"}}

